<template>
  <div class="clock">
    <!-- <pay-info v-show="ispay"></pay-info> -->
    <clock-in v-show="isclock"></clock-in>
  </div>
</template>

<script>
var uid = "oSORf5kkXvHNxhIx8lQVe3DFRFvw";
import payInfo from "../payInfo/App";
import clockIn from "../index/App";
export default {
  name: "clockIn",
  components: {
    payInfo: payInfo,
    clockIn: clockIn
  },
  data() {
    return {
      feedback: "", //用户反馈
      order: "", //订单信息
      orders: "", //订单列表
      cPerson: "",
      cAttendence: "",
      ispay: false, //是否显示合约页面
      isclock: false //是否显示打卡页面
    };
  },
  created() {
    this.Orders(this);
  },
  methods: {
    // 订单信息
    Orders(self) {
      this.$toast.loading({
        duraction: 0,
        forbidClick: true,
        loadingType: "circular",
        message: "数据加载中..."
      });
      TD_Request(
        "op",
        "eomp",
        { uid: uid },
        function(code, data) {
          console.log(data);
          self.ispay = true;
          self.feedback = data.feedback;
          var order = data.orders.shift();
          self.cPerson = data.cPerson;
          self.cAttendence = data.cAttendence;
          self.order = order;
          data.orders.push(order);
          self.orders = data.orders;
          self.$toast.clear();
          self.loop(self);
          console.log(data.orders);
        },
        function(code, data) {
          console.log(data);
          if (code == 82) {
            self.$toast.clear();
            // $("body").html("");
            // window.location.href = "clockIn.html#index?time=" + new Date().getTime();
            self.isclock = true;
            // return;
          }
        }
      );
    },
    loop(self) {
      setInterval(function() {
        var order = self.orders.shift();
        self.order = order;
        self.orders.push(order);
      }, 3000);
    }
  }
};
</script>

<style>
</style>



